<template><div class="markdown-body"><h1>步骤条</h1>
<p><code v-pre="">mu-stepper</code> 用途步骤导航，此组件只提供视图显示，步骤逻辑需要有使用者自行控制。</p>
<h2 id="shi-li"><a class="header-anchor" href="#shi-li" aria-hidden="true">¶</a> 示例</h2>
<p>一个活动发起的示例, 默认的步骤导航，是一步完成之后才能进行下一步的， 通过改变 <code v-pre="">active-step</code> 的值来控制当前步骤，使用的时候可根据 <code v-pre="">active-step</code> 展示相应的页面</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;div class=\&quot;demo-step-container\&quot;&gt;\n  &lt;mu-stepper :active-step=\&quot;activeStep\&quot;&gt;\n    &lt;mu-step&gt;\n      &lt;mu-step-label&gt;\n        选择活动地点\n      &lt;/mu-step-label&gt;\n    &lt;/mu-step&gt;\n    &lt;mu-step&gt;\n      &lt;mu-step-label&gt;\n        创建一个群组\n      &lt;/mu-step-label&gt;\n    &lt;/mu-step&gt;\n    &lt;mu-step&gt;\n      &lt;mu-step-label&gt;\n        宣传活动\n      &lt;/mu-step-label&gt;\n    &lt;/mu-step&gt;\n  &lt;/mu-stepper&gt;\n  &lt;div class=\&quot;demo-step-content\&quot;&gt;\n    &lt;p v-if=\&quot;finished\&quot;&gt;\n      都完成啦!&lt;a href=\&quot;javascript:;\&quot; @click=\&quot;reset\&quot;&gt;点这里&lt;/a&gt;可以重置\n    &lt;/p&gt;\n    &lt;template v-if=\&quot;!finished\&quot;&gt;\n      &lt;p&gt;\n        {{content}}\n      &lt;/p&gt;\n      &lt;div&gt;\n        &lt;mu-button flat class=\&quot;demo-step-button\&quot; :disabled=\&quot;activeStep === 0\&quot; @click=\&quot;handlePrev\&quot;&gt; 上一步 &lt;/mu-button&gt;\n        &lt;mu-button class=\&quot;demo-step-button\&quot; color=\&quot;primary\&quot; @click=\&quot;handleNext\&quot;&gt; {{activeStep === 2 ? '完成' : '下一步'}} &lt;/mu-button&gt;\n      &lt;/div&gt;\n    &lt;/template&gt;\n  &lt;/div&gt;\n&lt;/div&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><div class="demo-step-container">
  <mu-stepper :active-step="activeStep">
    <mu-step>
      <mu-step-label>
        选择活动地点
      </mu-step-label>
    </mu-step>
    <mu-step>
      <mu-step-label>
        创建一个群组
      </mu-step-label>
    </mu-step>
    <mu-step>
      <mu-step-label>
        宣传活动
      </mu-step-label>
    </mu-step>
  </mu-stepper>
  <div class="demo-step-content">
    <p v-if="finished">
      都完成啦!<a href="javascript:;" @click="reset">点这里</a>可以重置
    </p>
    <template v-if="!finished">
      <p>
        {{content}}
      </p>
      <div>
        <mu-button flat="" class="demo-step-button" :disabled="activeStep === 0" @click="handlePrev"> 上一步 </mu-button>
        <mu-button class="demo-step-button" color="primary" @click="handleNext"> {{activeStep === 2 ? '完成' : '下一步'}} </mu-button>
      </div>
    </template>
  </div>
</div>
</div>
                    
                    <div class="highlight" slot="highlight"><pre v-pre="" class="hljs"><code v-pre=""><span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-step-container"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">mu-stepper</span> <span class="hljs-attr">:active-step</span>=<span class="hljs-string">"activeStep"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-step</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-step-label</span>&gt;</span>
        选择活动地点
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-step-label</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-step</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-step</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-step-label</span>&gt;</span>
        创建一个群组
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-step-label</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-step</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-step</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-step-label</span>&gt;</span>
        宣传活动
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-step-label</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">mu-step</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">mu-stepper</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-step-content"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">p</span> <span class="hljs-attr">v-if</span>=<span class="hljs-string">"finished"</span>&gt;</span>
      都完成啦!<span class="hljs-tag">&lt;<span class="hljs-name">a</span> <span class="hljs-attr">href</span>=<span class="hljs-string">"javascript:;"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"reset"</span>&gt;</span>点这里<span class="hljs-tag">&lt;/<span class="hljs-name">a</span>&gt;</span>可以重置
    <span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">template</span> <span class="hljs-attr">v-if</span>=<span class="hljs-string">"!finished"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>
        {{content}}
      <span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">div</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">flat</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-step-button"</span> <span class="hljs-attr">:disabled</span>=<span class="hljs-string">"activeStep === 0"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handlePrev"</span>&gt;</span> 上一步 <span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
        <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-step-button"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleNext"</span>&gt;</span> {{activeStep === 2 ? '完成' : '下一步'}} <span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
      <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
    <span class="hljs-tag">&lt;/<span class="hljs-name">template</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="chui-zhi"><a class="header-anchor" href="#chui-zhi" aria-hidden="true">¶</a> 垂直</h2>
<p>垂直的步骤导航，步骤内容需要放置在 <code v-pre="">mu-step-content</code> 组件中，当 <code v-pre="">active-step</code> 改变时会显示不同的内容, <code v-pre="">mu-step-content</code> 只可以被用在垂直的步骤导航中</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;mu-container&gt;\n  &lt;div class=\&quot;demo-vsteper-container\&quot;&gt;\n    &lt;mu-stepper :active-step=\&quot;vactiveStep\&quot; orientation=\&quot;vertical\&quot;&gt;\n      &lt;mu-step&gt;\n        &lt;mu-step-label&gt;\n          选择活动地点\n        &lt;/mu-step-label&gt;\n        &lt;mu-step-content&gt;\n          &lt;p&gt;\n            在附近选择一个活动的地点, 最好靠近地铁站公交站，已方便交通，附近设施应尽量完善，最重要的是有Wifi\n          &lt;/p&gt;\n          &lt;mu-button class=\&quot;demo-step-button\&quot; @click=\&quot;vhandleNext\&quot; color=\&quot;primary\&quot;&gt;下一步&lt;/mu-button&gt;\n        &lt;/mu-step-content&gt;\n      &lt;/mu-step&gt;\n      &lt;mu-step&gt;\n        &lt;mu-step-label&gt;\n          创建一个群组\n        &lt;/mu-step-label&gt;\n        &lt;mu-step-content&gt;\n          &lt;p&gt;\n            创建群组，50人左右，以18-25单身青年为主。。。。。\n          &lt;/p&gt;\n          &lt;mu-button class=\&quot;demo-step-button\&quot; @click=\&quot;vhandleNext\&quot; color=\&quot;primary\&quot;&gt;下一步&lt;/mu-button&gt;\n          &lt;mu-button flat class=\&quot;demo-step-button\&quot; @click=\&quot;vhandlePrev\&quot;&gt;上一步&lt;/mu-button&gt;\n        &lt;/mu-step-content&gt;\n      &lt;/mu-step&gt;\n      &lt;mu-step&gt;\n        &lt;mu-step-label&gt;\n          宣传活动\n        &lt;/mu-step-label&gt;\n        &lt;mu-step-content&gt;\n          &lt;p&gt;\n            多在群里发消息宣传宣传，有事没事多在群里唠唠嗑，确定的话就ok拉\n          &lt;/p&gt;\n          &lt;mu-button class=\&quot;demo-step-button\&quot; @click=\&quot;vhandleNext\&quot; color=\&quot;primary\&quot;&gt;完成&lt;/mu-button&gt;\n          &lt;mu-button flat class=\&quot;demo-step-button\&quot; @click=\&quot;vhandlePrev\&quot;&gt;上一步&lt;/mu-button&gt;\n        &lt;/mu-step-content&gt;\n      &lt;/mu-step&gt;\n    &lt;/mu-stepper&gt;\n    &lt;p v-if=\&quot;vfinished\&quot;&gt;\n      都完成啦!&lt;a href=\&quot;javascript:;\&quot; @click=\&quot;vreset\&quot;&gt;点这里&lt;/a&gt;可以重置\n    &lt;/p&gt;\n  &lt;/div&gt;\n&lt;/mu-container&gt;\n\n\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      vactiveStep: 0\n    };\n  },\n  computed: {\n    vfinished () {\n      return this.vactiveStep &gt; 2;\n    }\n  },\n  methods: {\n    vhandleNext () {\n      this.vactiveStep++;\n    },\n    vhandlePrev () {\n      this.vactiveStep--;\n    },\n    vreset () {\n      this.vactiveStep = 0;\n    }\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.demo-vsteper-container{\n  max-width: 380px;\n  max-height: 400px;\n  margin: auto;\n}\n\n.demo-step-content {\n  margin: 0  16px;\n}\n\n.demo-step-button {\n  margin-top: 12px;\n  margin-right: 12px;\n}\n&quot;}">
                    <div class="source" slot="source"><mu-container>
  <div class="demo-vsteper-container">
    <mu-stepper :active-step="vactiveStep" orientation="vertical">
      <mu-step>
        <mu-step-label>
          选择活动地点
        </mu-step-label>
        <mu-step-content>
          <p>
            在附近选择一个活动的地点, 最好靠近地铁站公交站，已方便交通，附近设施应尽量完善，最重要的是有Wifi
          </p>
          <mu-button class="demo-step-button" @click="vhandleNext" color="primary">下一步</mu-button>
        </mu-step-content>
      </mu-step>
      <mu-step>
        <mu-step-label>
          创建一个群组
        </mu-step-label>
        <mu-step-content>
          <p>
            创建群组，50人左右，以18-25单身青年为主。。。。。
          </p>
          <mu-button class="demo-step-button" @click="vhandleNext" color="primary">下一步</mu-button>
          <mu-button flat="" class="demo-step-button" @click="vhandlePrev">上一步</mu-button>
        </mu-step-content>
      </mu-step>
      <mu-step>
        <mu-step-label>
          宣传活动
        </mu-step-label>
        <mu-step-content>
          <p>
            多在群里发消息宣传宣传，有事没事多在群里唠唠嗑，确定的话就ok拉
          </p>
          <mu-button class="demo-step-button" @click="vhandleNext" color="primary">完成</mu-button>
          <mu-button flat="" class="demo-step-button" @click="vhandlePrev">上一步</mu-button>
        </mu-step-content>
      </mu-step>
    </mu-stepper>
    <p v-if="vfinished">
      都完成啦!<a href="javascript:;" @click="vreset">点这里</a>可以重置
    </p>
  </div>
</mu-container>




</div>
                    
