          <span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-step-button"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"vhandleNext"</span> <span class="hljs-attr">color</span>=<span class="hljs-string">"primary"</span>&gt;</span>下一步<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
          <span class="hljs-tag">&lt;<span class="hljs-name">mu-button</span> <span class="hljs-attr">flat</span> <span class="hljs-attr">class</span>=<span class="hljs-string">"demo-step-button"</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"vhandlePrev"</span>&gt;</span>上一步<span class="hljs-tag">&lt;/<span class="hljs-name">mu-button</span>&gt;</span>
