<h2 id="fei-xian-xing-kong-zhi"><a class="header-anchor" href="#fei-xian-xing-kong-zhi" aria-hidden="true">¶</a> 非线性控制</h2>
<p><code v-pre="">linear</code> 设置为 false 时，不用完成上一步骤也可以查看下步骤的内容，<code v-pre="">mu-step-label</code> 需要被替换为 <code v-pre="">mu-step-button</code> 并监听点击事件显示改步骤内容</p>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;div class=\&quot;demo-step-container\&quot;&gt;\n  &lt;mu-stepper :active-step=\&quot;lactiveStep\&quot; :linear=\&quot;false\&quot;&gt;\n    &lt;mu-step :completed=\&quot;finishs.indexOf(0) !== -1\&quot;&gt;\n      &lt;mu-step-button @click=\&quot;changeStep(0)\&quot;&gt;\n        选择活动地点\n      &lt;/mu-step-button&gt;\n    &lt;/mu-step&gt;\n    &lt;mu-step :completed=\&quot;finishs.indexOf(1) !== -1\&quot;&gt;\n      &lt;mu-step-button @click=\&quot;changeStep(1)\&quot;&gt;\n        创建一个群组\n      &lt;/mu-step-button&gt;\n    &lt;/mu-step&gt;\n    &lt;mu-step :completed=\&quot;finishs.indexOf(2) !== -1\&quot;&gt;\n      &lt;mu-step-button @click=\&quot;changeStep(2)\&quot;&gt;\n        宣传活动\n      &lt;/mu-step-button&gt;\n    &lt;/mu-step&gt;\n  &lt;/mu-stepper&gt;\n  &lt;div class=\&quot;demo-step-content\&quot;&gt;\n      &lt;mu-button @click=\&quot;finishs.length === 3 ? (finishs = []) : finish()\&quot; color=\&quot;primary\&quot;&gt;{{finishs.length === 3 ? 'reset' : 'finish'}}&lt;/mu-button&gt;\n  &lt;/div&gt;\n&lt;/div&gt;\n\n\n\n&quot;,&quot;script&quot;:&quot;\nexport default {\n  data () {\n    return {\n      lactiveStep: 0,\n      finishs: []\n    };\n  },\n  methods: {\n    changeStep (index) {\n      this.lactiveStep = index\n    },\n    finish () {\n      if (this.finishs.indexOf(this.lactiveStep) === -1) this.finishs.push(this.lactiveStep);\n    }\n  }\n}\n&quot;,&quot;style&quot;:&quot;\n.demo-vsteper-container{\n  max-width: 380px;\n  max-height: 400px;\n  margin: auto;\n}\n\n.demo-step-container {\n  width: 100%;\n  max-width: 700px;\n  margin: auto;\n}\n\n.demo-step-content {\n  margin: 0  16px;\n}\n\n.demo-step-button {\n  margin-top: 12px;\n  margin-right: 12px;\n}\n&quot;}">
                    <div class="source" slot="source"><div class="demo-step-container">
  <mu-stepper :active-step="lactiveStep" :linear="false">
    <mu-step :completed="finishs.indexOf(0) !== -1">
      <mu-step-button @click="changeStep(0)">
        选择活动地点
      </mu-step-button>
    </mu-step>
    <mu-step :completed="finishs.indexOf(1) !== -1">
      <mu-step-button @click="changeStep(1)">
        创建一个群组
      </mu-step-button>
    </mu-step>
    <mu-step :completed="finishs.indexOf(2) !== -1">
      <mu-step-button @click="changeStep(2)">
        宣传活动
      </mu-step-button>
    </mu-step>
  </mu-stepper>
  <div class="demo-step-content">
      <mu-button @click="finishs.length === 3 ? (finishs = []) : finish()" color="primary">{{finishs.length === 3 ? 'reset' : 'finish'}}</mu-button>
  </div>
</div>



</div>
                    
