  <span class="hljs-tag">&lt;<span class="hljs-name">mu-stepper</span> <span class="hljs-attr">:active-step</span>=<span class="hljs-string">"lactiveStep"</span> <span class="hljs-attr">:linear</span>=<span class="hljs-string">"false"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">mu-step</span> <span class="hljs-attr">:completed</span>=<span class="hljs-string">"finishs.indexOf(0) !== -1"</span>&gt;</span>
      <span class="hljs-tag">&lt;<span class="hljs-name">mu-step-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"changeStep(0)"</span>&gt;</span>
        选择活动地点
      <span class="hljs-tag">&lt;/<span class="hljs-name">mu-step-button</span>&gt;</span>
