<h2 id="zi-ding-yi-tu-biao"><a class="header-anchor" href="#zi-ding-yi-tu-biao" aria-hidden="true">¶</a> 自定义图标</h2>
<demo-block :jsfiddle="{&quot;html&quot;:&quot;&lt;div class=\&quot;demo-step-container\&quot;&gt;\n  &lt;mu-stepper :activestep=\&quot;0\&quot; :linear=\&quot;false\&quot;&gt;\n    &lt;mu-step&gt;\n      &lt;mu-step-label&gt;\n        选择活动地点\n      &lt;/mu-step-label&gt;\n    &lt;/mu-step&gt;\n    &lt;mu-step&gt;\n      &lt;mu-step-label class=\&quot;demo-step-label-warning\&quot;&gt;\n        &lt;mu-icon slot=\&quot;icon\&quot; value=\&quot;warning\&quot; color=\&quot;red\&quot;&gt;&lt;/mu-icon&gt;\n        创建一个群组\n      &lt;/mu-step-label&gt;\n    &lt;/mu-step&gt;\n    &lt;mu-step&gt;\n      &lt;mu-step-label&gt;\n        宣传活动\n      &lt;/mu-step-label&gt;\n    &lt;/mu-step&gt;\n  &lt;/mu-stepper&gt;\n&lt;/div&gt;\n&quot;,&quot;script&quot;:null,&quot;style&quot;:null}">
                    <div class="source" slot="source"><div class="demo-step-container">
  <mu-stepper :activestep="0" :linear="false">
    <mu-step>
      <mu-step-label>
        选择活动地点
      </mu-step-label>
    </mu-step>
    <mu-step>
      <mu-step-label class="demo-step-label-warning">
        <mu-icon slot="icon" value="warning" color="red"></mu-icon>
        创建一个群组
      </mu-step-label>
    </mu-step>
    <mu-step>
      <mu-step-label>
        宣传活动
      </mu-step-label>
    </mu-step>
  </mu-stepper>
</div>
</div>
                    
