</div></demo-block>
<h2 id="stepper-props"><a class="header-anchor" href="#stepper-props" aria-hidden="true">¶</a> Stepper Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>active-step</td>
<td>当前步骤</td>
<td>Number</td>
<td>—</td>
<td>0</td>
</tr>
<tr>
<td>linear</td>
<td>是否为线性步骤导航，设置为 false，将不再控制 <code v-pre="">step</code> 组件的 completed 和 disabled 属性</td>
<td>Boolean</td>
<td>—</td>
<td>true</td>
</tr>
<tr>
<td>orientation</td>
<td>horizontal: 水平步骤导航, vertical: 垂直的步骤导航</td>
<td>String</td>
<td>—</td>
<td>horizontal</td>
</tr>
</tbody>
</table>
<h2 id="step-props"><a class="header-anchor" href="#step-props" aria-hidden="true">¶</a> Step Props</h2>
<table>
<thead>
<tr>
<th>参数</th>
<th>介绍</th>
<th>类型</th>
<th>可选值</th>
<th>默认值</th>
</tr>
</thead>
<tbody>
<tr>
<td>completed</td>
<td>是否已完成</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
<tr>
<td>disabled</td>
<td>是否被禁用</td>
<td>Boolean</td>
<td>—</td>
<td>false</td>
</tr>
</tbody>
</table>


</div class="markdown-body"></template>
<style>
.demo-vsteper-container{
  max-width: 380px;
  max-height: 400px;
  margin: auto;
}

.demo-step-container {
  width: 100%;
  max-width: 700px;
  margin: auto;
}

.demo-step-content {
  margin: 0  16px;
}

.demo-step-button {
  margin-top: 12px;
  margin-right: 12px;
}
</style>
<script>
export default {
  data () {
    return {
      activeStep: 0,
      vactiveStep: 0,
      lactiveStep: 0,
      finishs: []
    };
  },
  computed: {
    content () {
      let message = ''
      switch (this.activeStep) {
        case 0:
          message = '选择一个活动的地点';
          break
        case 1:
          message = '创建群组，50人左右';
          break
        case 2:
          message = '多在群里发消息宣传宣传';
          break
        default:
          message = 'fuck! 又 TM 出错了！！！';
          break
      }
      return message
    },
    lcontent () {
      let message = ''
      switch (this.lactiveStep) {
        case 0:
          message = '选择一个活动的地点';
          break
        case 1:
          message = '创建群组，50人左右';
          break
        case 2:
          message = '多在群里发消息宣传宣传';
          break
        default:
          message = 'fuck! 又 TM 出错了！！！';
          break
      }
      return message
    },
    finished () {
      return this.activeStep > 2;
    },
    vfinished () {
      return this.vactiveStep > 2;
    }
  },
  methods: {
    handleNext () {
      this.activeStep++;
    },
    handlePrev () {
      this.activeStep--;
    },
    reset () {
      this.activeStep = 0;
    },
    vhandleNext () {
      this.vactiveStep++;
    },
    vhandlePrev () {
      this.vactiveStep--;
    },
    vreset () {
      this.vactiveStep = 0;
    },
    changeStep (index) {
      this.lactiveStep = index
    },
    finish () {
      if (this.finishs.indexOf(this.lactiveStep) === -1) this.finishs.push(this.lactiveStep);
    }
  }
}
</script>


